import "core-js/modules/es.array.push.js";
// import "@/assets/scss/utils.scss"
export default {
  beforeRouteLeave(to, from, next) {
    clearInterval(this.inter);
    next();
  },

  data() {
    return {
      inter: null,
      countdown: 5
    };
  },

  mounted() {
    this.inter = setInterval(() => {
      this.countdown--;

      if (this.countdown == 0) {
        clearInterval(this.inter);
        this.goBack();
      }
    }, 1000);
  },

  methods: {
    goBack() {
      this.$router.push("/");
    }

  }
};